.review {
  display: flex;
  flex-basis: 33.33%;
}
.customer {
  position: relative;

  img {
    width: 100%;
  }

  p {
    margin-top: 1rem;

    display: inline-block;
    line-height: 145%;
    color: rgb(82, 82, 82);
  }

  margin: 1rem 1rem;
  padding: 1rem 2rem;
}
.cust-rev {
  margin: 2rem;
  h2 {
    text-align: center;
    font-size: 245%;
    color: #3d3d3d;
  }
}
.overlay-text {
  z-index: 1;
  margin: 1rem;
  padding: 0 2rem;
  position: absolute;
  top: 10%;
  left: 5%;
}
@media screen and (max-width: 780px) {
  .review {
    flex-direction: column;
  }
  .customer {
    img {
      width: 100%;
    }

    p {
      display: inline-block;
      font-size: 100%;
      line-height: 125%;
      color: rgb(82, 82, 82);
    }

    margin: 0;
    padding: 1rem 0.5rem;
  }
  .cust-rev {
    margin: 1rem;
    h2 {
      font-size: 140%;
    }
  }
  .overlay-text {
    margin: 1rem;
    font-size: 80%;
    line-height: 120%;
    padding: 0;
    top: 13%;
    left: 3%;
  }
}
