.toggle {
  display: none;
}
.navbar {
  display: block;
  margin: 0 auto;
  width: 90%;

  ul {
    div {
      img {
        width: 25%;
        vertical-align: middle;
      }
    }
    display: flex;
    flex-direction: row;
    justify-content: center;
    li {
      a {
        text-decoration: none;
        color: white;
      }
      display: inline-block;
      font-size: 120%;
      padding: 0 1.5rem;
      margin: auto 1rem;
    }
  }
}
.navigation {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 780px) {
  .navigation {
    width: 100%;
  }
  .navbar {
    margin: 0;
    width: auto;
    overflow: hidden;
    z-index: 1;
    ul {
      background-color: rgba(255, 119, 28, 0.9);
      flex-direction: column;
      justify-content: left;
      li {
        display: block;
        font-size: 80%;
        padding: 0 0.5rem;
        margin: 5px 0;
      }
    }
  }
  .toggle {
    display: block;
    overflow: hidden;

    button {
      background-color: rgba(255, 119, 28, 0.9);

      text-align: left;
      padding-left: 10px;
      font-size: 100%;
      color: #fff;
      width: 40rem;
      height: 2rem;
      background-color: transparent;
      border: none;
    }
  }
}
