.header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url(/img/ppr.jpg);
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  width: auto;
  margin: 0;
  display: flex;
  color: rgb(255, 255, 255);
  height: 60vh;
  position: relative;
  text-transform: uppercase;
}
.hero {
  height: fit-content;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  transform: translate(-50, -50);
  margin: auto;
  position: absolute;
  top: 15%;
  left: 10%;
}
.logo {
  display: flex;
  flex-wrap: wrap;
  width: fit-content;
  div {
    padding-left: 1rem;
    display: flex;
    flex-direction: column;
    h1 {
      font-family: 'Raleway', sans-serif;
      text-shadow: 4px 4px 4px rgb(36, 36, 36);
      font-size: 300%;
      font-weight: 700;
      padding: 1rem 0;
    }
  }
  img {
    width: 30%;
    height: auto;
    box-shadow: 4px 4px 4px rgb(36, 36, 36), -4px -4px 5px rgb(36, 36, 36);
  }
}
.btn,
.btn:link,
.btn:visited {
  text-decoration: none;
  color: rgb(255, 0, 0);
  font-size: 120%;
  font-weight: 600;
  background-color: rgb(255, 102, 0);
  border: 1px solid rgb(255, 196, 0);
  border-radius: 15px;
  padding: 10px 15px;
  width: fit-content;
  margin-left: 10px;
  animation: glowing 1300ms infinite;
}
@keyframes glowing {
  0% {
    background-color: rgb(255, 196, 0);
    box-shadow: 0 0 3px rgb(255, 196, 0);
  }
  50% {
    background-color: rgb(255, 196, 0);
    box-shadow: 0 0 10px rgb(255, 196, 0);
  }
  100% {
    background-color: rgb(255, 196, 0);
    box-shadow: 0 0 3px rgb(255, 196, 0);
  }
}
@media screen and(max-width:1200px) {
  .hero {
    padding: 1rem;

    top: 15%;
    left: 2%;
  }
  .logo {
    div {
      padding-left: 0.5rem;
      h1 {
        font-size: 230%;
        font-weight: 600;
        padding: 0.5rem 0;
      }
    }
    img {
      width: 28%;
    }
  }

  .btn,
  .btn:link,
  .btn:visited {
    font-size: 100%;
    font-weight: 500;
    padding: 7px 10px;
    margin-top: 10px;
  }
  .header {
    height: 50vh;
  }
}

@media screen and (max-width: 780px) {
  .header {
    height: 95vh;
  }
  .hero {
    padding: 0;
    width: 90%;
    top: 17%;
    left: 5%;
  }

  .logo {
    div {
      padding-left: 0rem;
      h1 {
        font-size: 180%;
        font-weight: 600;
        padding: 1rem 0;
        line-height: 125%;
      }
    }
    img {
      width: 100%;
    }
  }
  .btn,
  .btn:link,
  .btn:visited {
    font-size: 80%;
  }
}
