.btns,
.btns:link,
.btns:visited {
  text-decoration: none;
  color: #fff;
  font-size: 180%;
  margin: 0 1.5rem 0 1.5rem;
  border-radius: 10px;
  padding: 8px 15px;
  width: fit-content;
  text-shadow: none;

  margin-left: 10px;
}
.delivery {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/img/t.png);
  background-size: cover;
  background-attachment: fixed;
  color: #fff;
  text-shadow: 3px 2px 3px #000000;

  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  h2 {
    margin-top: 1.5rem;
    font-size: 400%;
  }
  h3 {
    margin-top: 1.5rem;
    font-size: 320%;
  }
}
.call-to-action {
  margin-top: 2.5rem;

  display: flex;
  flex-direction: row;
}
.btn-call {
  background-color: rgb(255, 30, 30);
}
.btn-wts {
  background-color: rgb(68, 165, 39);
}
.icon {
  font-size: 120%;
}
@media screen and (max-width: 1200px) {
  .btns,
  .btns:link,
  .btns:visited {
    font-size: 130%;
    margin: 0 1rem;
    padding: 6px 10px;
  }
  .delivery {
    padding: 1.5rem 0;

    h2 {
      margin-top: 1rem;
      font-size: 300%;
    }
    h3 {
      margin-top: 1rem;
      font-size: 220%;
    }
  }
  .icon {
    font-size: 100%;
  }

  .call-to-action {
    margin-top: 1.5rem;
  }
}

@media screen and (max-width: 780px) {
  .delivery {
    padding: 1rem 0;

    h2 {
      margin-top: 0.5rem;
      font-size: 220%;
      text-align: center;
      animation: blinkingText 1s infinite;
    }
    h3 {
      margin-top: 1rem;
      font-size: 170%;
      text-align: center;
    }
  }
  @keyframes blinkingText {
    0% {
      color: transparent;
    }
    49% {
      color: #fff;
    }
    60% {
      color: transparent;
    }
    99% {
      color: #fff;
    }
    100% {
      color: #fff;
    }
  }
  .call-to-action {
    display: flex;
    flex-direction: column;
    margin-top: 1.5rem;
    align-items: center;
  }
  .btns,
  .btns:link,
  .btns:visited {
    font-size: 120%;
    margin-bottom: 1rem;
    border-radius: 10px;
    padding: 5px 10px;
  }
}
