.features {
  background-color: rgb(243, 243, 243);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 3rem 0;
  h2 {
    font-size: 200%;
    text-align: center;
  }
}
.card {
  h3 {
    font-size: 140%;
    text-align: center;
  }
  p {
    line-height: 145%;
  }
  display: flex;
  flex-basis: 33.33%;
  margin: 2rem 3rem 0;
}
.offer {
  margin: 0 3rem;
  padding: 0.5rem 0.7rem;
  img {
    width: 100%;
    border-radius: 10px;
  }
}
@media screen and (max-width: 1200px) {
  .features {
    padding: 2rem 0;
    h2 {
      font-size: 180%;
    }
  }
  .card {
    h3 {
      font-size: 120%;
    }
    p {
      line-height: 125%;
      font-size: 90%;
    }

    margin: 1rem 1rem;
  }
  .offer {
    margin: 0 1rem;
    padding: 0.4rem 0.5rem;
  }
}
@media screen and (max-width: 780px) {
  .features {
    padding: 1.5rem 0;
    h2 {
      font-size: 150%;
    }
  }

  .card {
    h3 {
      font-size: 100%;
    }
    p {
      line-height: 125%;
      font-size: 80%;
    }
    flex-direction: column;
    margin: 1rem 0rem;
  }
  .offer {
    margin: 0.6rem 0;
    padding: 0.4rem 0.5rem;
  }
}
