.contact {
  padding: 2rem;
  display: flex;
  flex-direction: row;
}
.address {
  flex-basis: 50%;
  margin: 2rem;
  h2 {
    text-align: center;
    font-size: 150%;
    margin-bottom: 2rem;
  }
}
.map {
  iframe {
    border-radius: 10%;
  }
}
.footer {
  p {
    text-align: center;
    font-size: 80%;
  }
  background-color: rgb(243, 243, 243);
}
@media screen and(max-width:780px) {
  .contact {
    padding: 0.5rem;
    flex-direction: column;
  }
  .address {
    margin: 1rem 0;
    h2 {
      font-size: 120%;
      margin-bottom: 1rem;
    }
  }
  .map {
    iframe {
      width: 100%;
      height: 10rem;
    }
  }
  .footer {
    p {
      margin-top: 0.5rem;
      font-size: 70%;
    }
  }
}
