.about {
  display: flex;
  flex-direction: row;
  background-color: rgb(243, 243, 243);
}
.about-heading {
  background-color: rgb(243, 243, 243);
  font-size: 250%;
  text-align: center;
  color: rgb(71, 71, 71);
  padding: 2rem 0;
}
.profile {
  flex-basis: 33%;
  padding: 3rem;
  img {
    width: 400px;
    height: 400px;
    object-fit: cover;
    border-radius: 50%;
  }
}

.saying {
  flex-basis: 67%;
  padding: 3rem 3rem 3rem 1rem;
  margin: auto;
}
@media screen and (max-width: 780px) {
  .about {
    flex-direction: column;
  }
  .about-heading {
    font-size: 150%;
    padding: 1rem 0;
  }
  .profile {
    padding: 1rem;
    text-align: center;
    img {
      width: 200px;
      height: 200px;
    }
  }

  .saying {
    padding: 1rem;
  }
}
