.menu {
  display: flex;
  flex-basis: 33.33%;
}
.menu-list {
  margin: 0 auto;
  background-color: rgb(255, 119, 28);
  padding: 1rem 2rem;
  border-radius: 15px;
}
.menu-head {
  background-color: rgb(255, 255, 255);
  color: white;
  padding: 2rem;
  h2 {
    margin: 0;
    padding: 2rem;
    text-align: center;
    font-size: 250%;
    // font-weight: 800;
    color: rgb(255, 119, 28);
  }
  h4 {
    margin: 2rem auto;
    color: black;
    text-align: center;
    font-size: 120%;
  }
}
tr {
  font-size: 150%;
  line-height: 145%;
  width: 10rem;
}
.price {
  text-align: center;
}
.item {
  padding-right: 1rem;
}
.head-item {
  text-align: left;
  padding: 0.5rem 0;
  font-size: 100%;
}
.head-price {
  text-align: left;
  padding: 0.5rem 0;
}

@media screen and (max-width: 1200px) {
  .menu-list {
    padding: 0.5rem 1rem;
  }
  .menu-head {
    padding: 0.7rem;
    h2 {
      padding: 1rem;
      font-size: 200%;
    }
    h4 {
      margin: 1rem auto;
      font-size: 100%;
    }
  }
}
@media screen and (max-width: 780px) {
  .menu-head {
    padding: 1rem;
    h2 {
      margin: 0;
      font-size: 150%;
      // font-weight: 800;
      color: rgb(39, 91, 168);
    }
    h4 {
      margin: 2rem auto;
      font-size: 80%;
    }
  }
  .menu {
    display: flex;

    flex-direction: column;
  }
  .menu-list {
    padding: 0.5rem;
    width: 100%;
    margin: 0 auto 1.5rem;
  }

  .head-item {
    padding: 0 0 1rem 0;
    font-size: 70%;
  }
  .head-price {
    padding: 0 0 1rem 0;

    padding: 0;
  }
  tr {
    line-height: 105%;
  }
  .price {
    font-size: 60%;
  }
  .item {
    padding-right: 1.2rem;
    font-size: 60%;
  }
}
